
@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  background: transparent;
  outline: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}
address,
cite,
dfn,
em,
var {
  font-style: normal;
}
code,
kbd,
pre,
samp {
  font-family: courier new, courier, monospace;
}
ul,
ol {
  list-style: none;
}
a {
  color: #4990E2;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
fieldset,
img {
  border: 0;
}
button,
input,
select,
textarea {
  font-size: 100%;
  border: 0;
  border-radius: 0;
  -webkit-appearance: none;
  box-sizing: border-box;
}

/**
 * Created by sunchengbin on 16/3/9.
 * 项目中通用的东西
 */
body, button, input, select, option, td, textarea {
  font: 'Myriad Set Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 12px;
  color: #464646;
}
[data-dpr="2"] body, [data-dpr="2"] button, [data-dpr="2"] input, [data-dpr="2"] select, [data-dpr="2"] option, [data-dpr="2"] td, [data-dpr="2"] textarea {
    font-size: 24px;
}
[data-dpr="3"] body, [data-dpr="3"] button, [data-dpr="3"] input, [data-dpr="3"] select, [data-dpr="3"] option, [data-dpr="3"] td, [data-dpr="3"] textarea {
    font-size: 36px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
img {
  width: 100%;
  vertical-align: middle;
}
.block {
  display: block;
}
.clearfix:after {
  content: '\20';
  display: block;
  height: 0;
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.lazy {
  height: 0;
  padding-top: 100%;
  background-repeat: no-repeat;
  background-color: #f5f5f5;
  background-size: contain;
}
.icon {
  /* 通过设置 font-size 来改变图标大小 */
  width: 1em;
  height: 1em;
  /* 图标和文字相邻时，垂直对齐 */
  vertical-align: -0.15em;
  /* 通过设置 color 来改变 SVG 的颜色/fill */
  fill: currentColor;
  /* path 和 stroke 溢出 viewBox 部分在 IE 下会显示
     normalize.css 中也包含这行 */
  overflow: hidden;
}
[flex], [flex] > *, [flex] > [flex] {
  overflow: hidden;
}
[flex] {
  display: -ms-flexbox;
  display: flex;
}
[flex] > * {
  display: block;
}
[flex] > [flex] {
  display: -ms-flexbox;
  display: flex;
}
[flex~="dir:left"] {
  -ms-flex-direction: row;
  flex-direction: row;
}
[flex~="dir:right"] {
  -ms-flex-direction: row-reverse;
  flex-direction: row-reverse;
}
